<template>
  <div>
    <a-modal v-model="dialog" centered title="ค้นหาห้องประชุม">
      <a-spin :spinning="spinning">
        <div class="spin-content">
          <v-row class="pt-5" align="center" justify="center">
            <v-col>
              <a-alert
                :message="this.message_alert"
                description="โปรดกดเลือกเวลาด้านล่าง เพื่อค้นหาห้องประชุมที่ท่านต้องการ"
              />
            </v-col> </v-row
          ><br />

          <v-row
            align="center"
            justify="center"
            v-for="items in this.roomavailableStat"
            :key="items"
          >
            <v-col>
              <p>
                สถานะห้อง :
                <a-tag color="blue">
                  {{ "ห้อง : " + items.room }}
                </a-tag>
                <a-tag color="red" v-if="items.status == 'red'">
                  <span>สถานะ : </span>
                
                  <a-icon
                    type="frown"
                    theme="twoTone"
                    two-tone-color="#eb2f96"
                  />
                    <span> - มีผู้จองเต็ม</span>
                </a-tag>
                <a-tag color="orange" v-if="items.status == 'orange'">
                  <span>สถานะ : </span>
                  <a-icon type="meh" theme="twoTone" two-tone-color="#ffb517" />
                  <span> - มีผู้จองปานกลาง</span>
                </a-tag>
                <a-tag color="green" v-if="items.status == 'green'">
                  <span>สถานะ : </span>
                  <a-icon
                    type="smile"
                    theme="twoTone"
                    two-tone-color="#52c41a"
                  />
                      <span > - มีผู้จองน้อย </span>
                </a-tag>
              </p>
            </v-col>
          </v-row>

          <v-row class="" align="center" justify="center">
            <v-col class="" align="center" style="padding-bottom: 0px;">
              <a-form>
                <a-form-item label="เวลาเริ่มต้น">
                  <a-time-picker
                    inputReadOnly
                    :minute-step="1"
                    :disabledHours="disabledHours"
                    :disabledMinutes="disabledMinutes"
                    @change="onChange_Start_Time"
                    style="width: 100%"
                    :value="value_starttime"
                    format="HH:mm"
                  />
                </a-form-item>
              </a-form>
            </v-col>

            <v-col class="" align="center" style="padding-bottom: 0px;">
              <a-form>
                <a-form-item label="เวลาสิ้นสุด">
                  <div name="disabled-keyboard--input">
                    <a-time-picker
                      inputReadOnly
                      :minute-step="1"
                      :disabledHours="disabledHours"
                      :disabledMinutes="disabledMinutes"
                      @change="onChange_End_Time"
                      style="width: 100%"
                      :value="value_endtime"
                      format="HH:mm"
                    />
                  </div>
                </a-form-item>
              </a-form>
            </v-col>
          </v-row>
        </div>
      </a-spin>
      <template slot="footer">
        <a-button key="back" @click="closeDialog()">
          ปิด
        </a-button>
        <a-button key="submit" type="primary" @click="API_search_MeetingRoom()">
          ค้นหา
        </a-button>
      </template>
    </a-modal>

   
  </div>
</template>
<script>

import moment from "moment";
export default {
  components: {
   
  },
  data: () => ({
    dialog: false,
    select_to_date: null,
    value_starttime: null,
    value_endtime: null,
    start_time: null,
    end_time: null,
    delayTime: 500,
    spinning: false,
    date_not_convert_select: null,
    date_convert_select: "",
    loading: false,
    message_alert: "",
    source_api: {},
    data_value_room: null,
    data_dateNow: null,
    dayselect: null,
    roomavailableStat: null,
  }),
  methods: {
    moment,
    open(data) {
      this.date_not_convert_select = data;
      var dd = data.slice(8, 10);
      var mm = data.slice(5, 7);
      var yy = data.slice(0, 4);
      this.dayselect = dd;

      this.date_convert_select = dd + "-" + mm + "-" + yy;
      this.message_alert = "วันที่ท่านเลือก : " + this.date_convert_select;
      this.API_query_Status_Room();
      this.dialog = true;
    },
    closeDialog(){
      this.dialog =false
    },
    getDateNow() {
      let timeNow = new Date();
      if (timeNow.getDate() < 10 || timeNow.getMonth() + 1 < 10) {
        var dd = "0" + timeNow.getDate();
        var mm = "0" + (timeNow.getMonth() + 1);
      }
      this.data_dateNow = dd + "-" + mm + "-" + timeNow.getFullYear();
    },
    onChange_Select_Date(date, dateString) {
      if (dateString != null) {
        this.select_to_date = dateString;
      }
    },
    onChange_Start_Time(time, timeString) {
      this.value_starttime = time;
      this.start_time = timeString;
    },
    onChange_End_Time(time, timeString) {
      this.value_endtime = time;
      this.end_time = timeString;
    },
    disabledHours() {
      var hours = [];
      this.getDateNow();
      // console.log(this.date_convert_select);
      // console.log(this.data_dateNow);
      var dd_select = parseInt(this.date_convert_select.slice(0, 2));
      var dd_now = parseInt(this.data_dateNow.slice(0, 2));

      var mm_select = parseInt(this.date_convert_select.slice(4, 6));
      var mm_now = parseInt(this.data_dateNow.slice(4, 6));
      // console.log(mm_select,"+",mm_now);
      if (dd_select <= dd_now && mm_select <= mm_now) {
        for (let i = 0; i < moment().hour(); i++) {
          hours.push(i);
        }
      }
      return hours;
    },
    disabledMinutes(selectedHour) {
      var minutes = [];
      //  console.log(this.date_convert_select);
      // console.log(this.data_dateNow);
      var dd_select = parseInt(this.date_convert_select.slice(0, 2));
      var dd_now = parseInt(this.data_dateNow.slice(0, 2));

      var mm_select = parseInt(this.date_convert_select.slice(4, 6));
      var mm_now = parseInt(this.data_dateNow.slice(4, 6));
      // console.log(mm_select,"+",mm_now);

      if (
        selectedHour === moment().hour() &&
        dd_select <= dd_now &&
        mm_select <= mm_now
      ) {
        for (var i = 0; i < moment().minute(); i++) {
          minutes.push(i + 1);
        }
      }
      return minutes;
    },
    API_search_MeetingRoom() {
      // console.log(this.date_not_convert_select);
      // console.log(this.start_time);
      // console.log(this.end_time);
      
      this.spinning = true;
      var resStatus = "";
      if (this.start_time == null || this.end_time == null) {
        this.$message.error("กรุุณาเลือกข้อมูลให้ครบถ้วน");
      } else {
        this.source_api = {
          meeting_start: this.date_not_convert_select + " " + this.start_time,
          meeting_end: this.date_not_convert_select + " " + this.end_time,
        };
        // console.log(this.source_api);
        // console.log(this.$store.state.url_get_API_axios);
        this.axios({
          method: "POST",
          url:
            this.$store.state.url_get_API_axios + "api/v1/checkAvailableRoom",
          data: this.source_api,
        })

          .then((response) => {
            console.log(response);
            this.cleanSpinning();
            if (response.data.Status == "These Rooms are Available") {
              this.data_value_room = response.data.value;
              this.cleanSpinning();

               var data = {
                    date_not_convert_select: this.date_not_convert_select,
                    date_convert_select: this.date_convert_select,
                    start_time: this.start_time,
                    end_time: this.end_time,
                    room_source_api: this.data_value_room,
                  };
              console.log(data);
              this.$emit('getDialogAddMeeting',data);
              this.dialog = false;
            } else {
              this.Model_countDown_error();
              this.cleanSpinning();
            }
          })
          .catch((error) => {
            this.cleanSpinning();
            console.log(error.response.data);
            console.log(error.message);
            if (error.response.data.Status == "Datetime is Invalid") {
              resStatus = "Datetime is Invalid";
              this.cleanSpinning();
              this.Model_countDown_error(resStatus);
            } else if (
              error.response.data.Status ===
              "Start Time Should Be Less Then End Time"
            ) {
              this.cleanSpinning();
              resStatus = "Start Time Should Be Less Then End Time";
              this.Model_countDown_error(resStatus);
            } else if (error.message == "Network Error") {
              this.cleanSpinning();
              resStatus = "Network Error";
              this.Model_countDown_error(resStatus);
            }
          });
      }
    },
    API_query_Status_Room() {
      // console.log(this.date_not_convert_select);
      // console.log(this.start_time);
      var dd = "";
      var resStatus = "";
      if (this.dayselect.slice(0, 1) < 10 && this.dayselect.slice(0, 1) == 0) {
        dd = this.dayselect.slice(1, 2);
      } else {
        dd = this.dayselect;
      }
      this.spinning = true;

      this.axios({
        method: "GET",
        url: this.$store.state.url_get_API_axios + "api/v1/availableStat/" + dd,
      })

        .then((response) => {
          // console.log(response.data.Value);
          this.cleanSpinning();
          if (response.data.Status == "success") {
            this.roomavailableStat = response.data.Value[0].booking_sum_time;
            this.cleanSpinning();
          } else {
            resStatus = "not";
            this.Model_countDown_error(resStatus);
          }
        })
        .catch((error) => {
          this.cleanSpinning();
          console.log(error);
          resStatus = "Network Error";
          this.Model_countDown_error(resStatus);
        });
    },
    Model_countDown_error(resStatus) {
      let secondsToGo = 5;

      if (resStatus == "Datetime is Invalid") {
        const modal = this.$warning({
          title: "โปรดเลือกเวลาประชุมอีกครั้ง",
          content: `เนื่องจากเวลาเริ่มต้นของท่านน้อยกว่าปัจจุบัน`,
          centered: true,
        });
        const interval = setInterval(() => {
          secondsToGo -= 1;
          modal.update({
            content: `เนื่องจากเวลาเริ่มต้นของท่านน้อยกว่าปัจจุบัน`,
          });
        }, 1000);
        setTimeout(() => {
          clearInterval(interval);
          modal.destroy();
        }, secondsToGo * 1000);
      } else if (resStatus == "Start Time Should Be Less Then End Time") {
        const modal = this.$warning({
          title: "โปรดเลือกเวลาประชุมอีกครั้ง",
          content: `เนื่องจากเวลาเริ่มต้นของท่านมากกว่าเวลาสิ้นสุด`,
          centered: true,
        });
        const interval = setInterval(() => {
          secondsToGo -= 1;
          modal.update({
            content: `เนื่องจากเวลาเริ่มต้นของท่านมากกว่าเวลาสิ้นสุด`,
          });
        }, 1000);
        setTimeout(() => {
          clearInterval(interval);
          modal.destroy();
        }, secondsToGo * 1000);
      } else if (resStatus == "Network Error") {
        const modal = this.$warning({
          title: "ไม่สามารถเลือกห้องประชุมได้",
          content: `กรุณาตรวจสอบเครือข่าย`,
          centered: true,
        });
        const interval = setInterval(() => {
          secondsToGo -= 1;
          modal.update({
            content: `กรุณาตรวจสอบเครือข่าย`,
          });
        }, 1000);
        setTimeout(() => {
          clearInterval(interval);
          modal.destroy();
        }, secondsToGo * 1000);
      } else {
        const modal = this.$error({
          title: "ไม่สามารถค้นหาห้องประชุมได้",
          content: `โปรดเลือกเวลาประชุมอีกครั้ง`,
          centered: true,
        });
        const interval = setInterval(() => {
          secondsToGo -= 1;
          modal.update({
            content: `โปรดเลือกเวลาประชุมอีกครั้ง`,
          });
        }, 1000);
        setTimeout(() => {
          clearInterval(interval);
          modal.destroy();
        }, secondsToGo * 1000);
      }
    },
    cleanSpinning() {
      this.spinning = false;
    },

  },
};
</script>
<style scoped></style>
