<template>
  <div>
    <a-card style="">
      <v-row align="center" justify="center">
        <v-col class="mt-3 pt-0 pb-0" cols="7" align="left">
          <p>ปฏิทินการจองห้องประชุม</p>
        </v-col>

        <v-col class="mt-3 pt-0 pb-0" cols="" align="right">
          <a-icon class="pl-2" type="calendar" />
        </v-col>
      </v-row>
    </a-card>

    <v-row class="mt-1 pb-0">
      <v-col class="">
        <a-alert
          type="info"
          show-icon
          description="ท่านสามารถกดวันที่ในปฏิทิน เพื่อจองห้องประชุม"
        />
      </v-col>
    </v-row>
    <a-spin :spinning="spinning">
      <div class="spin-content">
        <v-row class="mt-1" align="center" justify="center">
          <v-col align="center">
            <div style="border: 1px solid #d9d9d9; border-radius: 4px">
              <a-calendar
                :fullscreen="false"
                :header-render="headerRender"
                :disabled-date="disabledDate"
                @select="onSelect"
                format="DD-MM-YYYY"
                @panelChange="onPanelChange"
              >
                <ul slot="dateCellRender" slot-scope="value" class="events">
                  <li v-for="item in getListData(value)" :key="item.content">
                    <v-icon x-small :color="item.color">
                      mdi-circle
                    </v-icon>

                    <!-- <a-icon
                  
                  :type="item.type"
                  :style="{ color: item.theme_color }"
                /> -->
                  </li>
                </ul>
              </a-calendar>
            </div>
          </v-col>
        </v-row>
      </div>
    </a-spin>
    <a-card class="mt-4">
      <v-row align="center" justify="center">
        <v-col cols="6" class="pa-0" align="center">
          <v-card-text class="pa-0 pt-2 font-weight-bold"
            >กดปุ่มดูการประชุมปัจจุปัน</v-card-text
          >
          <a-button
            class="mt-2"
            type="primary"
            @click="OpenDialogNowMeetingTable()"
          >
            <a-icon class="" type="schedule" />
            ห้องประชุมปัจจุบัน
          </a-button>

          <v-card-text class=" mt-3 pa-0 font-weight-bold"
            >กดปุ่มดูการจองของท่าน</v-card-text
          >

          <a-button
            class="mt-2"
            type="primary"
            @click="OpenDialogShowBookingMeeting()"
          >
            <a-icon class="" type="contacts" />
            ดูการจองของท่าน.
          </a-button>
        </v-col>
        <v-divider vertical inset></v-divider>

        <v-col cols="5" class="pa-0" align="right">
          <v-card-text class="pa-0 font-weight-bold">สถานะการจอง</v-card-text>
          <v-card-text class="pl-0 pb-0 pr-0">
            <v-icon class="mr-2" color="red">mdi-alert-circle</v-icon>
            หมายถึง เต็ม</v-card-text
          >
          <v-card-text class="pb-0 pr-0">
            <v-icon class="mr-2" color="green">mdi-alert-circle</v-icon>
            หมายถึง ว่าง</v-card-text
          >
        </v-col>
      </v-row>
    </a-card>
    <DialogSearchRoom ref="DialogSearchRoom"  @getDialogAddMeeting="OpenDialogAddMeeting"></DialogSearchRoom>
    <DialogAddMeeting ref="DialogAddMeeting"  @getAPI_GetBookingCalendar="API_GetBookingCalendar"></DialogAddMeeting>
    <DialogShowBookingMeeting
      ref="DialogShowBookingMeeting"

    ></DialogShowBookingMeeting>
    <DialogNowMeetingTable ref="DialogNowMeetingTable"></DialogNowMeetingTable>
  </div>
</template>
<script>
import DialogSearchRoom from "./DialogSearchRoom.vue";
import DialogAddMeeting from "./DialogAddMeeting.vue";
import DialogShowBookingMeeting from "./DialogShowBookingMeeting.vue";
import DialogNowMeetingTable from "./DialogNowMeetingTable.vue";
import moment from "moment";

export default {
  components: {
    DialogSearchRoom,
    DialogAddMeeting,
    DialogShowBookingMeeting,
    DialogNowMeetingTable,
  },
  data: () => ({
    List_booking_all: null,
    // List_booking_all: [
    //   {
    //     agenda: "ประชุม iot",
    //     booking_number: "96",
    //     eject_at: "2021-02-05 17:01:56",
    //     guest_email: "chatpeth.ke@one.th",
    //     meeting_end: "2021-02-05 19:00:00",
    //     meeting_start: "2021-02-05 18:00:00",
    //     meeting_start_day: 15,
    //     one_email: "waruenada.chu@one.th",
    //     room_num: "5-1_OAI",
    //   },

    //   {
    //     agenda: "ประชุม iot",
    //     booking_number: "96",
    //     eject_at: "2021-02-05 17:01:56",
    //     guest_email: "chatpeth.ke@one.th",
    //     meeting_end: "2021-02-05 19:00:00",
    //     meeting_start: "2021-02-05 18:00:00",
    //     meeting_start_day: 10,
    //     one_email: "waruenada.chu@one.th",
    //     room_num: "5-1_OAI",
    //     Month_now:null
    //   },
    //    {
    //     agenda: "ประชุม iot",
    //     booking_number: "96",
    //     eject_at: "2021-02-05 17:01:56",
    //     guest_email: "chatpeth.ke@one.th",
    //     meeting_end: "2021-02-05 19:00:00",
    //     meeting_start: "2021-02-05 18:00:00",
    //     meeting_start_day: 5,
    //     one_email: "waruenada.chu@one.th",
    //     room_num: "5-1_OAI",
    //     Month_now:null
    //   },
    //    {
    //     agenda: "ประชุม iot",
    //     booking_number: "96",
    //     eject_at: "2021-02-05 17:01:56",
    //     guest_email: "chatpeth.ke@one.th",
    //     meeting_end: "2021-02-05 19:00:00",
    //     meeting_start: "2021-02-05 18:00:00",
    //     meeting_start_day: 19,
    //     one_email: "waruenada.chu@one.th",
    //     room_num: "5-1_OAI",
    //     Month_now:null
    //   },
    // ],
    day_event_calendar: [3, 5],
    Date_now: null,
    Month_now: null,
    spinning: false,
  }),
  created() {
    this.getMonthNow();
    this.API_GetBookingCalendar();
  },
  methods: {
    moment,
    test(){
      console.log('what the error');
    },
    onSelect(value) {
      if (value != null) {
        var myJSONselectedValue = JSON.stringify(value);
        this.OpenDialogSearchRoom(myJSONselectedValue);
      } else {
        this.$message.error("กรุุณาเลือกวันที่ให้ถูกต้อง");
      }
    },
    onPanelChange(value, mode) {
      console.log(value, mode);
    },
    disabledDate(current) {
      return current && current < moment().startOf("day");
    },
    cleanSpinning() {
      this.spinning = false;
    },
    OpenDialogSearchRoom(myJSONselectedValue) {
      var selectedValue = myJSONselectedValue.slice(1, 11);
      this.$refs.DialogSearchRoom.open(selectedValue);
    },
    OpenDialogShowBookingMeeting() {
      this.$refs.DialogShowBookingMeeting.open();
    },
    OpenDialogAddMeeting(params) {
      this.$refs.DialogAddMeeting.open(params);
    },
    OpenDialogNowMeetingTable() {
      this.$refs.DialogNowMeetingTable.open();
    },
    getListData(value) {
      if (value.date() < 10) {
        var dateall = "0" + value.date();
      } else {
        dateall = value.date();
      }
      var dateValue = dateall + "-" + value.month();
      var dateSub = dateValue.substring(0, 2);
      var monthSub = dateValue.substring(3, 5);

      let listData;

      for (var items of this.List_booking_all) {
        if (
          dateSub.toString() == items.meeting_start_day.toString() &&
          dateSub > "0" + this.Date_now &&
          monthSub == (this.Month_now - 1).toString()
        ) {
          listData = [{ color: "#ff1761" }];
          break;
        } else {
          if (
            monthSub == (this.Month_now - 1).toString() &&
            dateSub >= "0" + this.Date_now
          ) {
            listData = [{ color: "#52c41a" }];
          }
        }
      }

      return listData || [];
    },
    getMonthNow() {
      let Monthnow = new Date();
      this.Month_now = Monthnow.getMonth() + 1;
      this.Date_now = Monthnow.getDate();
    },

    headerRender({ value, type, onChange, onTypeChange }) {
      const Now_Month = new Date(value);
      const Next_Month = new Date(value);

      const start = Now_Month.getMonth();
      const end = Next_Month.getMonth() + 1;
      const end_month = end;
      const monthOptions = [];

      const current = value.clone();
      const localeData = value.localeData();
      const months = [];
      for (let i = 0; i < 12; i++) {
        current.month(i);
        months.push(localeData.monthsShort(current));
      }
      // console.log(end_month);
      for (let index = start; index <= end; index++) {
        // console.log(index);
        // console.log(end);
        if (index <= end_month) {
          monthOptions.push(
            <a-select-option class="month-item" key={`${index}`}>
              {months[index]}
            </a-select-option>
          );
        }
      }
      const month = value.month();

      return (
        <div style={{ padding: "10px" }}>
          <a-row type="flex" justify="space-between">
            <a-col>
              <a-radio-group
                size="small"
                onChange={(e) => onTypeChange(e.target.value)}
                value={type}
              >
                <a-radio-button value="month">Month</a-radio-button>
              </a-radio-group>
            </a-col>
            <a-col>
              <a-select
                dropdownMatchSelectWidth={false}
                value={String(month)}
                onChange={(selectedMonth) => {
                  const newValue = value.clone();
                  newValue.month(parseInt(selectedMonth, 10));
                  onChange(newValue);
                }}
              >
                {monthOptions}
              </a-select>
            </a-col>
          </a-row>
        </div>
      );
    },
    API_GetBookingCalendar() {
    
      this.spinning = true;
      this.axios({
        method: "GET",
        url: this.$store.state.url_get_API_axios + "api/v1/bookingTable",
        // headers: { Authorization: "Bearer " + localStorage.token },
      })
        .then((response) => {
          this.cleanSpinning();
          // console.log(response);
          this.this_month = response.data.Value.this_month;
          this.next_month = response.data.Value.next_month;
          if (response.data.Status == "success") {
            this.List_booking_all = this.this_month;
            // console.log(this.List_booking_all);
          } else {
            // this.spinning = false;

            this.$error({
              title: "ไม่พบข้อมูล",
              content: "ไม่พบข้อมูล",
            });
          }
        })
        .catch((error) => {
          this.cleanSpinning();
          console.log(error.message);
          // this.loading = false;
          // if (error.message == "Request failed with status code 400") {
          //   this.$message.error("ไม่พบข้อมูล");
          // }
        });
    },
  },
};
</script>
<style scoped>
.events {
  list-style: none;
  margin: 0;
  padding: 0;
}
.events .ant-badge-status {
  overflow: hidden;
  white-space: nowrap;
  width: 100%;
  text-overflow: ellipsis;
  font-size: 12px;
}
.notes-month {
  text-align: center;
  font-size: 28px;
}
.notes-month section {
  font-size: 28px;
}
</style>
